import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/p1.png";
import projImg2 from "../assets/img/p2.png";
import projImg3 from "../assets/img/p3.1.png";
import projImg4 from "../assets/img/p4.png";
import projImg5 from "../assets/img/p6.png";
import projImg8 from "../assets/img/p8.png";
import projImg7 from "../assets/img/p7.png";
import colorSharp2 from "../assets/img/color-sharp2.png";
// import 'animate.css';
import TrackVisibility from 'react-on-screen';



export const Projects = () => {

  const projects = [
    {
      title: "Portfolio",
      description: "React",
      imgUrl: projImg1,
    },
    {
      title: "Order Site",
      description: "HTML & CSS & JS",
      imgUrl: projImg2,
    },
    {
      title: "Apartment Site",
      description: "React",
      imgUrl: projImg3,
    },
    {
      title: "Ecommerce",
      description: "React",
      imgUrl: projImg4,
    },
    {
      title: "Portfolio",
      description: "HTML & CSS & JS",
      imgUrl: projImg5,
    },
    {
      title: "Q&A ",
      description: "React",
      imgUrl: projImg8,
    },
    
  ];

  return (
    <section className="project" >
      <Container>
        <Row>
          <Col size={10}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn": ""}>
                
                <p></p>
                <Tab.Container id="projects-tabs" defaultActiveKey="first">
                <h2 id="project" className="show1">Projects</h2>
                
          
                  <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center " id="pills-tab">
                    {/* <Nav.Item>
                      <Nav.Link eventKey="first">ProJects</Nav.Link>
                    </Nav.Item> */}
                    {/* <Nav.Item>
                      <Nav.Link eventKey="second">Tab 2</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="third">Tab 3</Nav.Link>
                    </Nav.Item> */}
                  </Nav>
                  <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                    <Tab.Pane eventKey="first">
                      <Row>
                        {
                          projects.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                    {/* <Tab.Pane eventKey="section">
                      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque quam, quod neque provident velit, rem explicabo excepturi id illo molestiae blanditiis, eligendi dicta officiis asperiores delectus quasi inventore debitis quo.</p>
                    </Tab.Pane>
                    <Tab.Pane eventKey="third">
                      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque quam, quod neque provident velit, rem explicabo excepturi id illo molestiae blanditiis, eligendi dicta officiis asperiores delectus quasi inventore debitis quo.</p>
                    </Tab.Pane> */}
                  </Tab.Content>
                </Tab.Container>
              </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      {/* <img className="background-image-right" src={colorSharp2} ></img> */}
      
    </section>
  )
}